import React from "react";
import { Card, Typography, Space, Alert } from "antd";
import { useParams } from "react-router-dom";
import { useQuery } from "react-query3";
import { Endpoint } from "types";
import { setMethodColor } from "components/utils/utils";

export default function UrlCard() {
  const { id } = useParams();
  const { data: endpoint, isLoading } = useQuery<Endpoint>(`/endpoint/${id}`, { enabled: !!id });

  return (
    <Alert.ErrorBoundary>
      <Card bordered={true} loading={isLoading}>
        <Typography>
          <Space direction="horizontal" size="small">
            <Typography>
              <Space>
                {endpoint?.method?.map(x => <>
                  <Typography.Text
                    strong
                    style={{ color: setMethodColor(x) }}
                  >
                    {x.toUpperCase()}
                  </Typography.Text>
                </>)}
              </Space>
            </Typography>
            <Typography.Text>{endpoint?.url}</Typography.Text>
          </Space>
        </Typography>
      </Card>
    </Alert.ErrorBoundary>
  );
}
